import { useState, useEffect } from 'react';
import { useAddNewUserMutation } from './usersApiSlice';
import { useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSave } from '@fortawesome/free-solid-svg-icons';
import { ROLES } from '../../config/roles';
import useTitle from '../../hooks/useTitle';

const USER_REGEX = /^[a-zA-Z]{3,20}$/;
const PWD_REGEX = /^[A-z0-9!@#$%]{4,12}/;

export const NewUserForm = () => {
	useTitle('Nowy uytkownik');

	const [addNewUser, { isLoading, isSuccess, isError, error }] =
		useAddNewUserMutation();

	const navigate = useNavigate();

	const [username, setUsername] = useState('');
	const [validUsername, setValidUsername] = useState(false);
	const [password, setPassword] = useState('');
	const [validPassword, setValidPassword] = useState(false);
	const [roles, setRoles] = useState(['pracownik']);

	useEffect(() => {
		setValidUsername(USER_REGEX.test(username));
	}, [username]);
	useEffect(() => {
		setValidPassword(PWD_REGEX.test(password));
	}, [password]);

	useEffect(() => {
		if (isSuccess) {
			setUsername('');
			setPassword('');
			setRoles([]);
			navigate(`/dash/users`);
		}
	}, [isSuccess, navigate]);

	const onUsernmeChanged = (e) => setUsername(e.target.value);
	const onPasswordChanged = (e) => setPassword(e.target.value);

	const onRolesChanged = (e) => {
		const value = Array.from(
			e.target.selectedOptions,
			(option) => option.value
		);
		setRoles(value);
	};

	const canSave =
		[roles.length, validUsername, validPassword].every(Boolean) && !isLoading;

	const onSaveUserClicked = async (e) => {
		e.preventDefault();
		if (canSave) {
			await addNewUser({ username, password, roles });
		}
	};

	const options = Object.values(ROLES).map((role) => {
		return (
			<option key={role} value={role}>
				{role}
			</option>
		);
	});
	const errClass = isError ? 'errmsg' : 'offscreen';
	const validUserClass = !validUsername ? 'form__input--incomplete' : '';
	const validPwdClass = !validPassword ? 'form__input--incomplete' : '';
	const validRolesClass = !Boolean(roles.length)
		? 'form__input--incomplete'
		: '';

	const content = (
		<>
			<p className={errClass}>{error?.data?.message}</p>
			<form className='form' onSubmit={onSaveUserClicked}>
				<div className='form__title-row'>
					<h2>Nowy Uytkownik</h2>
					<div className='form__action-buttons'>
						<button className='icon-button' title='Save' disabled={!canSave}>
							<FontAwesomeIcon icon={faSave} />
						</button>
					</div>
				</div>
				<label className='form__label' htmlFor='username'>
					Nazwa:<span className='nowrap'>[3-20 liter]</span>
				</label>
				<input
					className={`form__input ${validUserClass}`}
					id='username'
					name='username'
					type='text'
					autoComplete='off'
					value={username}
					onChange={onUsernmeChanged}
				/>
				<label className='form__label' htmlFor='password'>
					Haso:
					<span className='nowrap'>
						[4-12 znakw w tym znak specjalny]
					</span>
				</label>
				<input
					className={`form__input ${validPwdClass}`}
					id='password'
					name='password'
					type='password'
					value={password}
					onChange={onPasswordChanged}
				/>
				<label className='form__label' htmlFor='roles'>
					Zaznacz role:
				</label>
				<select
					id='roles'
					name='roles'
					className={`form__select ${validRolesClass}`}
					multiple={true}
					size='3'
					value={roles}
					onChange={onRolesChanged}
				>
					{options}
				</select>
			</form>
		</>
	);

	return content;
};

export default NewUserForm;
